import React, { useState, useRef } from 'react';
import './index.less';

export default function Pagination() {
  const [currentPage, setCurrentPage] = useState(1);
  const ulElem = useRef();

  const selectPage = (page) => {
    if (page === currentPage) return;
    const currentElem = ulElem.current.querySelector(`li.page-number:nth-child(${currentPage+1})`);
    const activeElem = ulElem.current.querySelector(`li.page-number:nth-child(${page+1})`);
    currentElem.classList.remove('active');
    activeElem.classList.add('active');
    setCurrentPage(page);
  }

  return (
    <section className="pagination">
      <h4>Pagination</h4>
      <main>
        <ul ref={ulElem}>
          <li className="page-prev" onClick={() => selectPage(currentPage-1 > 0 ? currentPage-1 : currentPage)}>
            <button>
              <svg t="1638370837073" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" pid="10943" xmlnsXlink="http://www.w3.org/1999/xlink" width="28" height="28"><defs><style type="text/css"></style></defs><path d="M872 572H266.8l144.3-183c4.1-5.2 0.4-13-6.3-13H340c-9.8 0-19.1 4.5-25.1 12.2l-164 208c-16.5 21-1.6 51.8 25.1 51.8h696c4.4 0 8-3.6 8-8v-60c0-4.4-3.6-8-8-8z" p-id="10944" fill="#777777"></path></svg>
            </button>
          </li>
          <li className="page-number active" onClick={() => selectPage(1)}>
            <button>1</button>
          </li>
          <li className="page-number" onClick={() => selectPage(2)}>
            <button>2</button>
          </li>
          <li className="page-number" onClick={() => selectPage(3)}>
            <button>3</button>
          </li>
          <li className="page-number" onClick={() => selectPage(4)}>
            <button>4</button>
          </li>
          <li className="page-number" onClick={() => selectPage(5)}>
            <button>5</button>
          </li>
          <li className="page-number" onClick={() => selectPage(6)}>
            <button>6</button>
          </li>
          <li className="page-next" onClick={() => selectPage(currentPage+1 < 7 ? currentPage+1 : currentPage)}>
            <button>
              <svg t="1638370867943" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" pid="11788" xmlnsXlink="http://www.w3.org/1999/xlink" width="28" height="28"><defs><style type="text/css"></style></defs><path d="M873.1 596.2l-164-208C703 380.5 693.8 376 684 376h-64.8c-6.7 0-10.4 7.7-6.3 13l144.3 183H152c-4.4 0-8 3.6-8 8v60c0 4.4 3.6 8 8 8h695.9c26.8 0 41.7-30.8 25.2-51.8z" p-id="11789" fill="#777777"></path></svg>
            </button>
          </li>
        </ul>
      </main>
    </section>
  )
}
