import React, { useState, useRef} from 'react';
import { NavLink } from 'react-router-dom';
import './index.less';

export default function LeftSideBar() {
    
  const [hasShowMenu, setHasShowMenu] = useState(false);
  const leftSideBar = useRef();

  const showMenu = () => {
    if (hasShowMenu) {
      leftSideBar.current.classList.remove('left-side-bar-active');
    } else {
      leftSideBar.current.classList.add('left-side-bar-active');
    }
    setHasShowMenu(!hasShowMenu);
  }

  return (
    <React.Fragment>
      <div className="left-side-bar" ref={leftSideBar} onClick={showMenu}>
        <NavLink className="nav" to="/ProfileCard">Profile Card</NavLink>
        <NavLink className="nav" to="/WaveLoading">Wave Loading</NavLink>
        <NavLink className="nav" to="/LandInText">LandIn Text</NavLink>
        <NavLink className="nav" to="/RevealText">Reveal Text</NavLink>
        <NavLink className="nav" to="/Snow">Snow</NavLink>
        <NavLink className="nav" to="/ButtonBorderStroke">Button Border Stroke</NavLink>
        <NavLink className="nav" to="/HeaderWithSlideBar">Header With Slide Bar</NavLink>
        <NavLink className="nav" to="/FloatTextMenu">Float Text Menu</NavLink>
        <NavLink className="nav" to="/ButtonHoverShining">Button Hover Shining</NavLink>
        <NavLink className="nav" to="/TodoList">Todo List</NavLink>
        <NavLink className="nav" to="/NavTab">Nav Tab</NavLink>
        <NavLink className="nav" to="/Pagination">Pagination</NavLink>
      </div>
      <div className="menu-btn" onClick={showMenu}></div>
    </React.Fragment>
  )
}
