import React from 'react';
import './index.less';

export default function NavTab() {

  const selectNav = (event) => {
    const item = event.currentTarget;
    item.parentNode.childNodes.forEach(item => {
      item.classList.remove('nav-item-active');
    });
    item.classList.add('nav-item-active');
    item.parentNode.style.setProperty('--active-index', item.dataset.index);
  }

  return (
    <section className="navtab">
      <h4>Nav Tab</h4>
      <main>
        <div className="nav-list">
          <div className="nav-item nav-item-home nav-item-active" data-index={0} onClick={selectNav}>
            <span className="nav-icon"></span>
            <span className="nav-text">Home</span>
          </div>
          <div className="nav-item nav-item-explore" data-index={1} onClick={selectNav}>
            <span className="nav-icon"></span>
            <span className="nav-text">Explore</span>
          </div>
          <div className="nav-item nav-item-collection" data-index={2} onClick={selectNav}>
            <span className="nav-icon"></span>
            <span className="nav-text">Collection</span>
          </div>
          <div className="nav-item nav-item-help" data-index={3} onClick={selectNav}>
            <span className="nav-icon"></span>
            <span className="nav-text">Help</span>
          </div>
        </div>
      </main>
    </section>
  )
}
